.menu {
  display: none;
  position: absolute;
  z-index: 100;
  left: 0;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  background-image: linear-gradient(274deg, rgba(1, 101, 255, 0.2) 0%, rgba(1, 101, 255, 0.2) 100%);
  transform: translateX(100%);
  transition: transform 0.4s ease-out;
}
@media (max-width: 576px) {
  .menu {
    display: block;
  }
}
